import React, { FC, useEffect, useRef } from 'react'
import cl from './MessageForm.module.scss'
import debounce from "lodash/debounce";

interface MessageFormProps {
    message: string;
    setMessage: (state: string) => void;
    onSubmit: () => any;
    setIsUserTyping: (state: boolean) => void;
}


const MessageTextarea = ({ message, inputElementRef, onInputChange }) => {

    return (
        <div className={cl.message_textarea_wrapper}>

            <textarea
                placeholder='Message'

                value={message}

                ref={inputElementRef}

                className={cl.message_input}

                onChange={onInputChange}

                rows={1}
                wrap="off"
            />
        </div>
    )
}


const MessageForm: FC<MessageFormProps> = (props) => {

    const { message, setMessage, onSubmit, setIsUserTyping } = props

    const inputElementRef = useRef<HTMLTextAreaElement>(null)

    const formElementRef = useRef<HTMLFormElement>(null)


    const handleIsTyping = debounce(function () {
        // continually delays setting "isTyping" to false for 500ms until the user has stopped typing and the delay runs out
        setIsUserTyping(false);
    }, 850);

    const onInputChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        e.preventDefault()

        if (inputElementRef.current) {
            inputElementRef.current.style.height = "auto";
            let scHeight = inputElementRef.current.scrollHeight;
            inputElementRef.current.style.height = `${scHeight}px`
        }

        setMessage(e.target.value)

        setIsUserTyping(true)
        handleIsTyping()
    }

    const onSubmitForm = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault()

        onSubmit() // function that we are receiving in props

        if (inputElementRef.current) {
            inputElementRef.current.style.height = "auto";
            inputElementRef.current.focus()
        }
    }

    return (
        <form
            ref={formElementRef}
            className={cl.formWrapper}
            onSubmit={onSubmitForm}
        >
            <div className={cl.smiles_button_wrapper}>
                <div className={cl.smiles_button}>
                    :]
                </div>
            </div>

            <MessageTextarea
                message={message}
                inputElementRef={inputElementRef}
                onInputChange={onInputChange}
            />

            {message.trim() ?
                <button
                    type='submit'
                    className={cl.button}
                >
                    Send
                </button>
                :
                <div
                    className={cl.openSendFile}
                >
                    file
                </div>
            }

        </form>
    )
}

export default MessageForm