import { FC } from 'react'
import cl from './Message.module.scss';

interface Idata {
    message: string;
    username: string;
}

interface MessageProps {
    data: Idata;
    isOutgoing: boolean;
}

interface MessageInfoProps {
    isOutgoing: boolean,
    isMessageLoading?: boolean
}

const MessageInfo: FC<MessageInfoProps> = ({ isOutgoing }) => {

    const isMessageLoading = false

    const seen = true

    return (
        <div className={cl.message_info}>
            <span className={cl.time}>
                12:13
            </span>

            {(() => {
                if (isOutgoing) {
                    if (isMessageLoading) {
                        return (
                            <span>
                                Loading...
                            </span>
                        )
                    } else {
                        if (seen) {

                            return (
                                <span>
                                    +
                                </span>
                            )
                        } else { // not seen
                            console.assert(true)
                            return (
                                <span>
                                    -
                                </span>
                            )
                        }
                    }
                }
            })()}

        </div>
    )
}


const Message: FC<MessageProps> = (props) => {
    const { data, isOutgoing } = props

    return (
        <div className={`${cl.main_wrapper} ${isOutgoing ? cl.outgoing : cl.incoming}`}>
            <div className={`${cl.content_wrapper}`}>
                <div className={cl.content}>
                    {!isOutgoing &&
                        <span className={cl.username}>
                            Alanominator
                        </span>
                    }
                    <div className={cl.message_text}>
                        {data.message}

                        <MessageInfo isOutgoing={isOutgoing} />

                    </div>
                </div>
            </div>
        </div>
    )
}

export default Message;